@mixin atSmall{
    @media (min-width: 375px){
        @content
    }
}

@mixin atMedium{
    @media (min-width: 750px){
        @content
    }
}

@mixin atLarge {
    @media (min-width: 1200px) {
        @content
    }
}

@mixin atExtraLarge {
    @media (min-width: 2000px) {
        @content
    }
}