.logo{
    font-size: 25px;
    color: $white;
    padding: 20px 0;
    display: inline-block;
    a{
        text-decoration: none;
        &:visited{
            color: $white;
        }
    }
}

.favorites{
    color: $white;
    float: right;
    margin: 20px 5px 0 0;
    padding: 6px 0;
    width: 85px;
    text-align: center;
    border-radius: 5px;
    outline: none;
    border: none;
    border: 1px solid rgb(248, 248, 122);
    cursor: pointer;
    text-decoration: none;
    Font-size: 15px;
    color: rgb(248, 248, 122);
    transition: .1s transform ease-in-out;
    &:hover{
       transform: scale(1.1);
       transition: .1s transform ease-in-out;
       border: 1px solid yellow;
       color: yellow;
       
    }
}

.attribution{
    background: #0A0C1C;
    padding: 10px;
    text-align: center;
    font-size: 11px;
    a{
        color: gray;
    }
}