#loadingScreen{
    width: 100%;
    height: 100%;
    position: fixed;
    background-color: $main;
    z-index: 999;
    #box-container{
        width: 150px;
        margin: 0 auto;
        position: relative;
        top: 40%;
        transform: translateY(-50%);
    }
    .text{
        width: 100%;
        text-align: center;
        color: white;
        font-size: 20px;
        font-family: 'Ubuntu', sans-serif;
        letter-spacing: 1px;
        position: relative;
        top: 40%;
        transform: translateY(-50%);
    }
    .boxes{
        width: 50px;
        height: 50px;
        background: red;
        position: relative;
    }
    .box1{
        position: relative;
        animation: moveBox1 1s ease-in-out infinite;
        left: 0;
    }
    @keyframes moveBox1{
        0%{
            left: 0;
        }
        25%{
            top: 0;
            left: 100px;
        }
        50%{
            left: 100px;
            top: 100px;
        }
        75%{
            top: 100px;
            left: 0;
        }
        100%{
            top: 0;
            left: 0;
        }
    }
      
    .box2{
        background: white;
        left: 50px;
    }
    .box3{
        animation: moveBox3 1s ease-in-out infinite;
        /*   left: 50px; */
    }
    
    @keyframes moveBox3{
        0%{
        top: 0;
        left: 0;
        }
        25%{
        top: -100px;
        left: 0;
        }
        50%{
        top: -100px;
        left: 100px;
        }
        75%{
        top: 0;
        left: 100px;
        }
        100%{
        top: 0;
        left: 0;
        }
    }      
}